class Board extends React.Component {
    constructor(props) {
        super(props);
        var x = 3;
        var y = 3;

        //TODO get this from local storage, or scramble a new one
        let puzzleState = [1, null, 2, 3, 4, 5, 6, 7, 8];
        this.state = { squares: puzzleState, cols: x, rows: y, width: window.innerWidth, height: window.innerHeight };

        setInterval(() => {
            this.setState({width: window.innerWidth, height: window.innerHeight});
        }, 2000);

        setInterval(() => {
            
        }, 2000);
    }


    minIndex(distanceList) {
        let result = -1;
        let min = 999999;
        for (let i = 0; i < distanceList.length; i++) {
            if (distanceList[i] < min) {
                result = i;
                min = distanceList[i];
            }
        }
        return [result, min];
    }

    findNearest(geo, cells) {
        let result = -1;
        let min = 9999999999;
        for (var i = 0; i < cells.length; i++) {
            const dist = this.geoDistance(geo, cells[i]);   
            if (dist < min) {
                min = dist;
                result = i;
            }
        }
        return result;
    }

    neighborsOf(i) {
        var result = [];
        var w = this.state.rows;
        var row = Math.floor(i / this.state.rows);

        //Down one row
        console.log("w  and len" + [w, this.state.squares.length]);
        if (i + w < this.state.squares.length) {
            result.push(i + w)
        }
        //Up one 
        if (i - w >= 0) {
            result.push(i - w);
        }
        //Right
        if (Math.floor((i + 1) / w) === row) {
            result.push(i + 1);
        }
        //Left
        if (i > 0 && Math.floor((i - 1) / w) === row) {
            result.push(i - 1);
        }

        return result;
    }
    
    handleClick(index) {
        this.updateNearest();
        const squares = this.state.squares.slice();

        let neighbors = this.neighborsOf(index);
       // alert(neighbors);

        //TODO need to check if move is allowed!!!
        const moveAllowed = DEBUG("DISABLE_MOVE_CHECK") || (index === this.nearest);

        if (moveAllowed) {
            for (let i = 0; i < neighbors.length; i++) {
                if (squares[neighbors[i]] == null) {
                    console.log("Found a match! Can move " + index + " to " + neighbors[i]);
                    squares[neighbors[i]] = squares[index];
                    squares[index] = null;
                    break;
                }
            }
        }

        this.setState({...this.state, squares: squares });
    }

    renderSquare(i, nearest) {
        const aspect = 4/3;
        let width = 0.9*(window.innerWidth) / 3;
        let height = 0.9*(window.innerHeight) / 3;
        if (width / height > aspect) {
            width = height * aspect;
        }
        else {
            height = width / aspect;
        }
        return <Square
        img={imgHome + this.props.gameData.img}
        value={this.state.squares[i]} 
        isNearest={i === this.nearest}
        geoUser={this.props.geoUser}
        geoCenter={this.props.gameData.cells[i]}
        handleClick={() => this.handleClick(i)} 
        w = {width}
        h = {height}
        rows = {this.state.rows}
        cols = {this.state.cols}
        />;
    }

    updateNearest() {
        this.nearest = -1;
        var distanceList = [];
        if (this.props.hasLocation) {
            distanceList = this.props.gameData.cells.map(cell => Geo.distanceInKm(this.props.geoUser, cell));
            this.nearest = this.minIndex(distanceList)[0];
        }
    }

    render() {
        this.updateNearest();
        return (
            <center>
            <table className="grid">
                <tr className="board-row">
                    {this.renderSquare(0)}
                    {this.renderSquare(1)}
                    {this.renderSquare(2)}
                </tr>
                <tr className="board-row">
                    {this.renderSquare(3)}
                    {this.renderSquare(4)}
                    {this.renderSquare(5)}
                </tr>
                <tr className="board-row">
                    {this.renderSquare(6)}
                    {this.renderSquare(7)}
                    {this.renderSquare(8)}
                </tr>
            </table>
            </center>
        );
    }
}

function DebugCoords(props) {
    return (
        <fieldset>
            <legend> Debug Coordinates </legend>
            <input value={props.coords[0] + ", " + props.coords[1]} onChange={props.onChange} style={{width: "100%"}}/>            
        </fieldset>
    )
}

class Game extends React.Component {
    constructor(props) {    
        super(props);

        let puzzle = window.location.pathname.substr(1);

        this.state = {hasLocation: false, 
            coords: [44, -63],
            serverData: null,
            puzzle: puzzle
        };

        if (puzzle.length > 0) {
            const json = jsonHome + puzzle + ".json";
            fetch(json)
            .then( response => response.json())
            .then( data => this.setState({ serverData : data }))        
            .catch( (err) => this.setState({ fetchError: "Failed to fetch: " + json }));
        }

        //TODO need to update this often!
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(position => {
                this.setState({coords: [position.coords.latitude, position.coords.longitude], hasLocation: true});
            });
        } else {
            this.setState({hasLocation: false});
        }

        this.debugChangeCoord = this.debugChangeCoord.bind(this);
    }

    debugChangeCoord(e) {
        const newCoords = e.target.value.split(",").map(f => parseFloat(f));
        this.setState({ coords: newCoords });
    }

    render() {
        if (this.state.fetchError) {
            return <div> {this.state.fetchError} </div>;
        }
        if (this.state.serverData === null) {
            return <div> Loading...</div>;
        }
        return (
            <div> 
                { DEBUG("SET_GPS") ? <DebugCoords coords={this.state.coords} onChange={this.debugChangeCoord}/> : null }
                <Board gameData={this.state.serverData} geoUser={this.state.coords} hasLocation={this.state.hasLocation}/>
            </div>
        );
    }
}

// ========================================

ReactDOM.render(
    <Game />,
    document.getElementById('root')
);
